  top: 0;
  width: 100%;
  z-index: 999;
  position: fixed;
}

.title-wrapper {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  bottom: 8px;
  width: 100%;
}

.nav-title {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  color: #111;
}

.content-item-left {
  text-align: left;
  display: flex;
  text-align: center;
  /* flex: 0.3; */
}

.content-item-center {
  align-items: left;
  text-align: left;
}

.content-item-right {
  text-align: right;
  /* flex: 0.3; */
}

.left-text-color {
  margin-left: 10px;
}

.text-white {
  color: #fff;
}

.text-black {
  color: #000;
}

.img-mode {
  background-size: 100% 100%;
}

.no-bg {
  background-color: #fff;
}

span{
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

div{
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@font-face {
  font-family: "iconfont";
  src: url("data:application/font-woff;charset=utf-8;base64,")
    format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 25px;
  padding-top:3px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-fanhui:before {
  content: "\e641";
